


































import Vue from 'vue'
import { Checkbox, Divider } from 'vant'

export default Vue.extend({
  name: 'about',
  data () {
    return {
      checked: true,
      unchecked: false
    }
  },
  components: {
    [Checkbox.name]: Checkbox,
    [Divider.name]: Divider
  }
})
